<script>
import http from "../http-common";

export default {
  name: 'Companies',
  data(){
    return{
      users:[],
      fields:[
        {key:'co_NO', label:'No.'},
        {key:'co_ID', label:'ID'},
        {key:'company', label:''},
        {key:'category_ID', label:''},
        {key:'size_ID', label:''},/* 7-22 CO_SIZE -> SIZE_ID */
        {key:'hr', label:'HR'},
        {key:'co_EMAIL', label:'E-mail'}/* 7-22 SIZE_ID -> co_EMAIL */
      ],
      filter: ''
    }
  },
  methods:{
    retrieveUsers(){
      http
        .get("/comp-list")
        .then(response=>{
          this.users = response.data;
          console.log(response.data);
          console.log(response.data[2]);
        })
        .catch(e=>{
          console.log(e);
        })
    },
  CompanyDetail(row)
 {
      http
      .post("/checkcompanydetail",
      {
        co_ID: row.co_ID,
        co_PW:row.co_PW,
        company:row.company,
        co_EMAIL:row.co_EMAIL,
        hr:row.hr,
        size_ID:row.size_ID,
        category_ID:row.category_ID,
      
     })
      .then((response) => 
      {
        console.log(response);
        if (response.data)
        {
          console.log(response.data);
          this.$router.push({name: "CompanyDetail",query: {co_ID:row.co_ID}});
        } else{
          console.log(response.data);
          alert("  ");
        }
      })
      .catch((e) => {
        console.log(e);
        alert(" x");
      })
    }
  }
  ,
  mounted(){
    this.retrieveUsers();
  }
