<template>
  <div class="register-pos">
    <b-form>
      <b-container fluid>
        <!--  DB   -->
        <b-row style="padding:30px;">
          <b-col md="12">
            <h2 align="left"> DB </h2>
          </b-col>
        </b-row>

        <!--  DB  -->
        <!-- ( ) -->
        <b-row style="padding:10px;">
          <b-col sm="2">
            <label for="input-pos"> </label>
          </b-col>
          <b-col sm="10" style="width:600px">
            <b-form-input
              id="input-pos"
              type="text"
              v-model="form.posName"
              placeholder="Enter Job Posting Title"
              required
            ></b-form-input>
          </b-col>
        </b-row>
        <!--   -->
        <b-row style="padding:10px;">
          <b-col sm="2">
            <label for="input-qualify"> </label>
          </b-col>
          <b-col sm="10" style="width:300px">
            <b-form-select
              id="input-qualify"
              v-model="form.qualify"
              :options="qualifyOptions"
              required
            ></b-form-select>
          </b-col>
        </b-row>
        <!--   -->
        <b-row style="padding:10px;">
          <b-col sm="2">
            <label for="input-employtype"> </label>
          </b-col>
          <b-col sm="10" style="width:300px">
            <b-form-select
              id="input-employtype"
              v-model="form.employType"
              :options="employTypeOptions"
              required
            ></b-form-select>
          </b-col>
        </b-row>
        <!--  ( ) -->
        <b-row style="padding:10px;">
          <b-col sm="2">
            <label for="datepicker-deadline"> </label>
          </b-col>
          <b-col sm="10" style="width:300px">
            <b-form-datepicker
              id="datepicker-deadline"
              v-model="form.deadline"
              close-button
              required
            ></b-form-datepicker>
          </b-col>
        </b-row>
        <!--   -->
        <b-row style="padding:10px;">
          <b-col sm="2">
            <label for="textarea-task"> </label>
          </b-col>
          <b-col sm="10" style="width:1000px">
            <b-form-textarea
              id="textarea-task"
              v-model="form.taskInfo"
              rows="8"
              max-rows="8"
              required
            ></b-form-textarea>
          </b-col>
        </b-row>
      </b-container>
      <!--  ()  -->
      <b-button type="button" variant="primary" @click="register">  </b-button>
    </b-form>
  </div>
</template>

<script>
import http from "../http-common";

export default {
  name: "RegisterPos",
  data() {
    return {
      form: {
        posName: "",
        qualify: "",
        employType: "",
        deadline: "",
        taskInfo: "",
      },
      qualifyOptions: [
        { text: "", value: "" },
        { text: " ", value: "" },
      ],
      employTypeOptions: [
        { text: "", value: "" },
        { text: "", value: "" },
      ],
      co_no: -1,
      co_id: null,
    };
  },
  methods: {
    register() {
      this.getCO_ID();
    },
    getCO_NO() {
      //   id co_no 
      http
        .get("/comp-number", { params: { co_id: this.co_id } })
        .then((response) => {
          console.log(response.data);
          this.co_email = response.data;
          if (this.co_email != null){/*8-03 */
            this.registerPosition();
          } 
          else {
            alert(" DB  ..");
            this.$router.go(0);
          }
        })
        .catch((e) => {
          console.log(e);
          this.co_email = -1;
        });
    },
    getCO_ID() {
      //      id 
      http
        .post("/check-session")
        .then((response) => {
          console.log(response.data);
          console.log(' id ');
          if (response.data != null && response.data != "") {
            this.co_id = response.data;
            this.getCO_NO();
          } else {
            this.co_id = null;
          }
        })
        .catch((e) => {
          console.log(e);
          this.co_id = null;
        });
    },
    registerPosition() {
      http
        .post("/register-position", {
          position: this.form.posName,
          qualify: this.form.qualify,
          employ_TYPE: this.form.employType,
          regi_DT: this.form.deadline,
          job: this.form.taskInfo,
          co_EMAIL: this.co_no,
          co_ID:this.co_id 
        })
        .then((response) => {
          console.log(response.data);
          alert(" DB  !");
          this.$router.push({ name: "JobPosting" });
        })
        .catch((e) => {
          console.log(e);
          alert(" DB  ..");
          this.$router.go(0);
        });
    },
  },
};
</script>

<style scoped></style>
