<template>
  <div class="jobposting">
    <!--    -->
    <div>
       <b-container fluid>
         <!--   -->
         <b-row style="padding:10px;">
           <!--   -->
           <b-col md="6">
             <h2 align="left"> </h2>
           </b-col>
           <!--  DB  (  ) -->
           <b-col md="3">
              <div v-show="user_code == 'qwe'">
                <b-button variant="primary" href="/#/register-position"> DB </b-button>
              </div>
             
           </b-col>
           <!--  -->
           <b-col md="3">
             <b-input-group size="sm">
               <b-form-input
                id="filter-input"
                v-model="filter"
                type="search"
                placeholder="///"></b-form-input>
             </b-input-group>
           </b-col>
          </b-row>
         <!--   table -->
          <b-row>
             <b-table hover :items="contents" :filter="filter" :fields="fields" @row-clicked="JobDetail"></b-table>
          </b-row>
       </b-container>
    </div>
  </div>
</template>

<script>
import http from "../http-common";

export default {
  name: 'JobPosting',
  data(){
    return{
      contents:[],
      fields:[
        {key:'post_NO', label:'No.'},
        {key:'company', label:''},
        {key:'position', label:' '},
        {key:'qualify', label:''},
        {key:'employ_TYPE', label:''},
        {key:'regi_DT', label:''}
      ],
      filter: '',
      user_code:'asd'
    }
  },
  methods:{
    retrieveContents(){
      http
        .get("/job-posting")
        .then(response=>{
          this.contents = response.data;
          console.log(response.data);
        })
        .catch(e=>{
          console.log(e);
        })
    },
    checkUserCode(){
      http
        .post("/check-usercode")
        .then(response=>{
          console.log(response.data);
          if(response.data){
            this.user_code = 'qwe'
          }
          else{
            this.user_code = 'asd';
          }
        })
        .catch(e=>{
          console.log(e);
        })
    },
    JobDetail(row)
    {
    
      http
      .post("/checkdetail",
      {
        post_NO: row.post_NO,
        company: row.company,
        position: row.position,
        qualify: row.qualify,
        employ_TYPE: row.employ_TYPE,
        regi_DT:row.regi_DT
      })
      .then((response) => 
      {
        console.log(response);
        if (response.data)
        {
          console.log(response.data);
          console.log(row.post_NO);
          this.$router.push({name: "JobDetail",query: {post_NO:row.post_NO}});
        } else{
          console.log(response.data);
          alert("  ");
        }
      })
      .catch((e) => {
        console.log(e);
        alert(" x");
      })
    }
  }
  
  ,
  mounted(){
    this.retrieveContents();
    this.checkUserCode();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  padding: 30px
}
</style>