<template>
  <div class="login-comp">
    <b-form>
      <b-card title=" ">
        <!-- ID -->
        <b-form-group label="ID" label-for="input-id">
          <b-form-input
            id="input-id"
            type="text"
            v-model="form.id"
            placeholder="Enter ID"
            required
          ></b-form-input>
        </b-form-group>

        <!--  -->
        <b-form-group label="Password" label-for="input-pw">
          <b-form-input
            id="input-pw"
            type="password"
            v-model="form.pw"
            placeholder="Enter Password"
            required
            @keyup.enter="loginComp"
          ></b-form-input>
        </b-form-group>

        <b-button type="button" variant="primary"  @click="loginComp">  </b-button>
      </b-card>
    </b-form>
  </div>
</template>

<script>
import http from "../http-common";

export default {
  name: "LoginComp",
  data() {
    return {
      form: {
        id: "",
        pw: "",
      },
    };
  },
  methods: {
    loginComp() {
      http
        .post("/login", {
          id: this.form.id,
          pw: this.form.pw,
          user_code: 1,
        })
        .then((response) => {
          if (response.data) {
            console.log(response.data);
            alert(" ");
            this.$router.push({ name: "Home" });
          //  this.$router.go(0);
          } else {
            console.log(response.data);
            alert(" ..ID/PW  .");
           // this.$router.go(0);
          }
        })
        .catch((e) => {
          console.log(e);
          alert(" .. ..");
          //this.$router.go(0);
        });
    },
  },
};
</script>

<style>
.login-comp {
  position: absolute;
  top: 380px;
  left: 50%;
  width: 400px;
  margin: -150px 0 0 -200px;
}
</style>
